.servers-wrapper{padding:16px}.servers-wrapper .gauge-model{width:100%;height:360px;overflow:hidden;margin:0 auto}.servers-wrapper .gauge-model .gauge-echart{width:350px;margin:auto auto -80px}.servers-wrapper .gauge-model .text-1{width:100%;margin:0 auto;font-size:32px;text-align:center}.servers-wrapper .text-num-model.text{width:100%;height:380px;overflow:hidden;border:1px solid #e8e8e8;margin:0 auto 16px}.servers-wrapper .text-num-model.text>.title{height:36px;line-height:36px;background:#f9f9f9;border-bottom:1px solid #e8e8e8;padding-left:10px}.servers-wrapper .text-num-model.text>.title span{width:100%;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;display:block}.servers-wrapper .text-num-model .value-p{height:276px;line-height:276px;text-align:center}.servers-wrapper .text-num-model .value-p>strong{font-size:100px;color:#333}.servers-wrapper .text-num-model .text-1{width:100%;margin:0 auto;font-size:32px;text-align:center}.servers-wrapper.mysql-model .text-num-model{height:250px}.servers-wrapper.mysql-model .text-num-model .text-1{font-size:18px}.servers-wrapper.mysql-model .text-num-model .value-p{height:150px;line-height:150px}.servers-wrapper.mysql-model .text-num-model .value-p>b{font-size:72px}.servers-wrapper.mysql-model .text-num-model .value-p>.state>em{font-size:50px}.servers-wrapper.mysql-model .text-num-model .value-p>.state .success{color:#3c0}.servers-wrapper.mysql-model .text-num-model .value-p>.state .error{color:red}.servers-wrapper .row-box{width:100%;height:380px;overflow:hidden;border:1px solid #e8e8e8;margin:0 auto 16px}.servers-wrapper .row-box .row-title{height:36px;line-height:36px;background:#f9f9f9;border-bottom:1px solid #e8e8e8;position:relative}.servers-wrapper .row-box .row-title span{font-size:12px;color:#444}.servers-wrapper .row-box .row-title span.sp{margin-right:6px}.servers-wrapper .row-box .row-title .left{position:absolute;left:10px;top:0}.servers-wrapper .row-box .row-title .right{position:absolute;right:10px;to